import 'ScssComponents/recipes-card-mixin.scss';
import 'ScssComponents/featured-recipe.scss';
import 'ScssComponents/product-pairs.scss';
import Swiper, { Navigation, EffectFade } from 'swiper';
import { exist, onWindowResize, getWindowSize } from '../utils';
import { BREAKPOINTS } from '../utils/constants';
import 'swiper/css';
import 'swiper/css/effect-fade';

const featuredRecipe = (
	sliderSelector = '.js-featured-recipe-slider',
	btnNextSelector = '.js-featured-recipe-next-button',
	btnPrevSelector = '.js-featured-recipe-prev-button',
) => {
	Swiper.use([Navigation, EffectFade]);

	const $slider = document.querySelector(sliderSelector);
	const $btnNext = document.querySelector(btnNextSelector);
	const $btnPrev = document.querySelector(btnPrevSelector);

	if (!exist([$slider, $btnNext, $btnPrev])) return;
	let featuredRecipeSwiper;

	const initSwiper = () => {
		return new Swiper($slider, {
			speed: 800,
			loop: true,
			effect: 'fade',
			fadeEffect: {
				crossFade: true,
			},
			navigation: {
				nextEl: $btnNext,
				prevEl: $btnPrev,
			},
		});
	};

	onWindowResize(() => {
		if (getWindowSize().windowWidth < BREAKPOINTS.mediaPoint1) {
			if (!featuredRecipeSwiper) {
				console.log('-');
				featuredRecipeSwiper = initSwiper();
			}
		} else {
			if (featuredRecipeSwiper) {
				console.log('+');
				featuredRecipeSwiper.destroy();
				featuredRecipeSwiper = null;
			}
		}
	});
};

export default featuredRecipe;
