import Swiper, { Navigation } from 'swiper';
import { exist } from '../utils';
import 'ScssComponents/products-slider.scss';
import 'ScssComponents/product-card.scss';
import 'ScssComponents/universal/section-head.scss';
import 'swiper/css';

const productSlider = (
	sectionSelector = '.js-products-section',
	sliderSelector = '.js-products-slider',
	btnNextSelector = '.js-products-next-button',
	btnPrevSelector = '.js-products-prev-button',
) => {
	const $sections = document.querySelectorAll(sectionSelector);
	if (!exist($sections)) return;

	Swiper.use([Navigation]);

	$sections.forEach($section => {
		const $slider = $section.querySelector(sliderSelector);
		const $btnNext = $section.querySelector(btnNextSelector);
		const $btnPrev = $section.querySelector(btnPrevSelector);
		if (!exist([$slider, $btnNext, $btnPrev])) return;

		const productsSwiper = new Swiper($slider, {
			speed: 800,
			navigation: {
				nextEl: $btnNext,
				prevEl: $btnPrev,
			},
			breakpoints: {
				320: {
					slidesPerView: 2,
				},
				768: {
					slidesPerView: 3,
				},
				1024: {
					slidesPerView: 4,
				},
			},
		});
	});
};

export default productSlider;
