import Swiper, { Navigation, Thumbs } from 'swiper';
import { Fancybox } from '@fancyapps/ui';
import '@fancyapps/ui/dist/fancybox.css';
import { exist } from '../utils';
import 'ScssComponents/product-gallery.scss';

import 'swiper/css';

const singleGallerySlider = (
	mainSliderSelector = '.js-product-gallery-main',
	thumbSliderSelector = '.js-product-gallery-thumbs',
	btnNextSelector = '.js-gallery-next-button',
	btnPrevSelector = '.js-gallery-prev-button',
) => {
	const $mainSlider = document.querySelector(mainSliderSelector);
	const $thumbSlider = document.querySelector(thumbSliderSelector);
	if (!exist([$mainSlider, $thumbSlider])) return;

	Swiper.use([Navigation, Thumbs]);

	const $btnNext = document.querySelector(btnNextSelector);
	const $btnPrev = document.querySelector(btnPrevSelector);

	const thumbsSwiper = new Swiper($thumbSlider, {
		spaceBetween: 10,
		slidesPerView: 3,
		freeMode: true,
		watchSlidesProgress: true,
	});

	const mainSwiper = new Swiper($mainSlider, {
		spaceBetween: 0,
		navigation: {
			nextEl: $btnNext,
			prevEl: $btnPrev,
		},
		thumbs: {
			swiper: thumbsSwiper,
		},
	});
};

export default singleGallerySlider;
